import Vue from 'vue'
import Vuelidate from 'vuelidate'
import Uimini from 'uimini/dist/css/uimini.css'

import App from './App.vue'
import router from './router'
import store from './store'

import firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'
import 'firebase/messaging'
import 'firebase/storage'

Vue.use(
  Vuelidate,
  Uimini
)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  created() {
    var firebaseConfig = {
      apiKey: "AIzaSyA_4YYPK1fiYBN7dQOQfbmQDe_OQV97Neo",
      authDomain: "time-diary-92213.firebaseapp.com",
      databaseURL: "https://time-diary-92213.firebaseio.com",
      projectId: "time-diary-92213",
      storageBucket: "",
      messagingSenderId: "568070054394",
      appId: "1:568070054394:web:dab28ac556476f55"
    };
    // Initialize Firebase
    firebase.initializeApp(firebaseConfig);

    firebase.auth().onAuthStateChanged(user => {
      if (user) {
        // Check Logged
        this.$store.dispatch('loggedUser', user)
        // Loading All Tasks
        this.$store.dispatch('loadTasks')
        // Loading All Tags
        this.$store.dispatch('loadTags')
        console.log(this.$store.getters.message.title)
      }
    })
  },
  render: function (h) { return h(App) }
}).$mount('#app')
